import React, { useState } from "react";
import Swal from "sweetalert2";
import { useNavigate } from "react-router-dom";
import { useData } from "./DataContext";
import FormHelperText from '@mui/material/FormHelperText';
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers";
import { PrimaryButton } from "./components/PrimaryButton";
import { MainContainer } from "./components/MainContainer";
import { Form } from "./components/Form";
import { Input2 } from "./components/RTLinput";
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';
import Checkbox from '@mui/material/Checkbox';
import FormGroup from '@mui/material/FormGroup';
import Grid from "@material-ui/core/Grid";
import Stack from '@mui/material/Stack';
import Box from '@mui/material/Box';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import MenuItem from '@mui/material/MenuItem';
import * as yup from "yup";
import ReactHookFormSelect from "./components/ranks"
import {RealTypo} from "./components/RealTypo"


const schema = yup.object().shape({
  firstName: yup
    .string()
    .matches(/^([^0-9]*)$/, "砖 驻专    住驻专")
    .required("  转 砖 驻专"),
  lastName: yup
    .string()
    .matches(/^([^0-9]*)$/, "砖 砖驻    住驻专")
    .required("  转 砖 砖驻"),
  isDati: yup
    .boolean(),
    datiORishylabel: yup
    .string()
    .required(" 专 转 住 拽砖")
  ,
  shapeOfBeard: yup
    .string()
    .required(" 专 转 住 拽")
  ,
  ranks: yup
    .string()
    .required(" 专 转 专")
  ,
  date: yup
    .string()

});



export const Step1 = () => {
  const [value, setValue] = useState(new Date());

  const { setValues, data } = useData();
  const history = useNavigate();
  const [RegOffice, setRegOffice] = useState(data.datiORishylabel);
  const handleChangeRegOffice = (event) => {
    if (event.target.value === 'ishy')
      {data["isDati"] = false;
      setChecked(false);
      }
    setRegOffice(event.target.value);
  };

  const [RegOffice2, setRegOffice2] = useState(data.shapeOfBeard);
  const handleChangeRegOffice2 = (event) => {
    setRegOffice2(event.target.value);
  };
  if (data["isDati"] === undefined)
  {
    data["isDati"] = false;
  }
  console.log(data["isDati"])
  if (data["isDati"] === undefined) {
    console.log("isdati is undefined")
    data["isDati"] = false;
  }
  const [checked, setChecked] = React.useState(JSON.parse(data["isDati"]));

  const handleChange = (event) => {
    setChecked(event.target.checked);
  };
  const { register, handleSubmit, errors, control } = useForm({
    defaultValues: {
      firstName: data.firstName,
      lastName: data.lastName,
      datiORishylabel: data.datiORishylabel,
      shapeOfBeard: data.shapeOfBeard,
      isDati: data.isDati,
      ranks: data.ranks,
      date: data.date
    },
    mode: "onBlur",
    resolver: yupResolver(schema),
  });


  const onSubmit = async (data) => {
    const { value: accept } = await Swal.fire({
      title: '爪专转 :',
      width: 600,
      html: `
      <style>
      li {
  font-size: 90%;
  text-align: right;
}
sub {
  font-size: 50%;
  text-align: right;
}
right-align {
  text-align: right;
}
.swal2-header {
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;

}
.swal2-content {
  font-family: 'Open Sans', sans-serif;
  text-align: right;
}
</style>
      <ul class="right-align" dir='rtl'>
      
 <p> <li>拽  砖驻  拽 转, 转  转.
  </li></p>
  <p><li>  转 转  拽驻 注 拽  砖驻 住专  转 驻注 爪转 转.
  </li></p>
  <p> <li> 注 ,    转 拽  砖驻, 转专 专   (注  转  住 专驻转,   砖专 专驻 爪注 注 ).
  </li></p>
  <p> <li> 注  砖转 转 驻住 砖专 专  注转 爪 砖专 爪, 驻拽   ,  专砖 .
  </li></p>
  <sub >转: 拽状 33-05-01 - 状 驻注 砖状 注 98 住驻 壮</sub>
  </ul> `,
      input: 'checkbox',
      inputValue: 0,
      padding: '1em',
      inputPlaceholder :
        ' 砖专 转 爪专转.',
      confirmButtonText:
        '砖 <i class="fa fa-arrow-right"></i>',
      inputValidator: (result) => {
        return !result && ' 砖专 转 爪专  砖!'
      }
    })
    
    if (accept) {
      console.log(data);
      setValues(data);
      history("/Signature");
    }
  };


  console.log(data)

  return (
    <MainContainer >
      <RealTypo component="h2" variant="h5">驻专 拽砖</RealTypo>
      <Stack
        spacing={40}
      >
        <Form onSubmit={handleSubmit(onSubmit)}>
          <Box dir='rtl' m={2}>
            <Grid container
              direction="row"
              alignItems="center"
              spacing={2}>

              <Grid item container
                direction="row"
                alignItems="flex-end"
                justify="center" md={4}>
                <Input2
                  dir="rtl"
                  ref={register}
                  id="firstName"
                  type="text"
                  label="砖 驻专"
                  name="firstName"
                  error={!!errors.firstName}
                  style={{textAlign:"right", marginRight: '0px'}}
                  helperText={errors?.firstName?.message}
                />
              </Grid>

              <Grid item container
                direction="row"
                alignItems="flex-end"
                justify="center" md={4}>
                <Input2
                  dir="rtl"
                  ref={register}
                  id="lastName"
                  type="text"
                  label="砖 砖驻"
                  name="lastName"
                  error={!!errors.lastName}
                  style={{textAlign:"right", marginRight: '0px'}}
                  helperText={errors?.lastName?.message}
                />

              </Grid>

              <Grid item container
                direction="row"
                alignItems="flex-end"
                justify="center" md={4}>


                  <ReactHookFormSelect
                      error={!!errors.ranks}
                      value={errors?.ranks?.message}
                      id="ranks"
                      name="ranks"
                      label="专"
                      dir='rtl'
                      control={control}
                      defaultValue=""
                >
                      <MenuItem value="专">专</MenuItem>
                      <MenuItem value="专状">专状</MenuItem>
                      <MenuItem value="住">住</MenuItem>
                  <MenuItem value="住状专">住状专</MenuItem>
                 
                </ReactHookFormSelect>
               


              </Grid>

            </Grid>
          </Box>
          <Box dir='rtl' m={2}>
          <Grid item>
          <FormControl error={!!errors.datiORishylabel} dir='rtl'>
            <FormLabel id="datiORishylabel">住 拽砖</FormLabel>
            <RadioGroup>
              <FormControlLabel control={
                <Radio
                  name="datiORishylabel"
                  inputRef={register}
                  value="ishy"
                  checked={RegOffice === "ishy"}
                  onChange={handleChangeRegOffice}
                />} label="注 砖" />
              <FormControlLabel control={
                <Radio
                  name="datiORishylabel"
                  inputRef={register}
                  value="dati"
                  checked={RegOffice === "dati"}
                  onChange={handleChangeRegOffice}
                />} label="注 砖 - 转" />
            </RadioGroup>
            <FormHelperText style={{textAlign:"right"}}>{errors?.datiORishylabel?.message}</FormHelperText>
          </FormControl>
          </Grid>
          <Box dir='rtl' m={2}>
          <Grid item>
          <FormControl error={!!errors.shapeOfBeard} dir='rtl'>
            <FormLabel id="shapeOfBeard">爪专转 拽  砖驻:</FormLabel>
            <RadioGroup >
              <FormControlLabel control={
                <Radio
                  name="shapeOfBeard"
                  inputRef={register}
                  value="Full"
                  checked={RegOffice2 === "Full"}
                  onChange={handleChangeRegOffice2}
                />} label="拽  注转 驻转 专 注 住专 ( 砖驻)" />
              <FormControlLabel control={
                <Radio
                  name="shapeOfBeard"
                  inputRef={register}
                  value="French"
                  disabled = {RegOffice === 'dati'}
                  checked= {RegOffice2 === "French" && RegOffice !== 'dati'} 
                  onChange={handleChangeRegOffice2}
                />} label="拽 拽 (爪专驻转) - 砖转 爪注 驻, 专 注 住专" />
              <FormControlLabel control={
                <Radio
                  name="shapeOfBeard"
                  inputRef={register}
                  value="Mos"
                  checked={RegOffice2 === "Mos"}
                  onChange={handleChangeRegOffice2}
                />}
                label="砖驻" />
            </RadioGroup>
            <FormHelperText style={{textAlign:"right"}}>{errors?.shapeOfBeard?.message}</FormHelperText>
          </FormControl>
          </Grid>
          </Box>
            <Grid item>
              <FormLabel id="isDatilabel"> 转 转?</FormLabel>
              <FormControlLabel control={<Checkbox
                disabled= {RegOffice !== 'dati'}
                checked={checked}
                onChange={handleChange}
                inputRef={register}
                name="isDati"
              />} label=" 拽 专  转" />
            </Grid>
          </Box>

          <Grid item>
            <LocalizationProvider dateAdapter={AdapterDateFns}>

              <DatePicker
                dir="rtl"
                label="转专"
                id="date"
                inputFormat="dd/MM/yyyy"
                disabled
                value={value}
                inputRef={register}
                onChange={(newValue) => {
                  setValue(newValue);
                }}
                renderInput={(params) => <Input2 name="date"{...params} />}
              />
            </LocalizationProvider>
          </Grid>


            

          <PrimaryButton></PrimaryButton>
        </Form>
        </Stack>
    </MainContainer>
  );
};
